import React from "react";
import "./OtherMain.css";
import OtherContent from "./OtherContent";

function OtherMain() {
  const otherList = [
    {
      ow_title: "Fantasia Realism",
      ow_by: "Sotun and Vera",
      ow_year: 2022,
      ow_role: "ML Technician",
      ow_image: "o-machine_cover.gif",
    },
    {
      ow_title: "Fantasia Realism",
      ow_by: "Sotun and Vera",
      ow_year: 2022,
      ow_role: "ML Technician",
      ow_image: "o-machine_cover.gif",
    },
  ];

  const mapOther = otherList.map((elm) => {
    return (
      <OtherContent
        ow_title={elm.ow_title}
        ow_by={elm.ow_by}
        ow_year={elm.ow_year}
        ow_role={elm.ow_role}
        ow_image={elm.ow_image}
      />
    );
  });

  return (
    <div>
      <p>A list of projects I have been apart of!</p>
      <div className="other-main-content">{mapOther}</div>
    </div>
  );
}

export default OtherMain;
